@import url(https://fonts.googleapis.com/css?family=Gothic+A1:400,700,900|Open+Sans&display=swap);
:root {
  --black: #0D0D0D;
  --lightest-grey: #E6E6E6;
  --light-grey: #D5D5D5;
  --grey: #939393;
  --dark-grey: rgb(121, 121, 121);
  --synth1Color: #26C6C6;
  --bassColor: #FF6AEE;
  --lennyColor: yellow;
  --drummerColor: green;
}

.piano-roll {
  height: 100%;
  display: flex;
  flex-flow: row nowrap;
  justify-content: flex-start;
  margin: auto;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.piano-roll-editor {
  min-height: 0px;
  display: flex;
  flex-flow: column-reverse nowrap;
  justify-content: flex-end;
  flex-grow: 1;
}

.piano-roll-keys {
  min-height: 0px;
  width: 35px;
  display: flex;
  flex-flow: column-reverse nowrap;
  justify-content: flex-end;
}

.step-row {
  min-height: 0px;
  height: 100%;
  display: flex;
  flex-flow: row nowrap;
  align-items: center;
}

.step-row.black {
  background-color: #939393;
  background-color: var(--grey);
}

.step-row.white {
  background-color: #E6E6E6;
  background-color: var(--lightest-grey);
}
.step-row.white:nth-of-type(odd) {
  background-color: #D5D5D5;
  background-color: var(--light-grey);
}

.piano-cell {
  display: flex;
  flex-flow: column nowrap;
  justify-content: center;
  align-items: center;
  box-sizing: border-box;
  width: 100%;
  height: 100%;
  min-height: 0px;
  border-right: 1px solid rgb(121, 121, 121);
  border-right: 1px solid var(--dark-grey);
  font-family: 'Roboto';
  font-size: 10px;
}

.piano-key {
  min-height: 0px;
  padding: 0px;;
  margin: 0px;
  display: flex;
  flex-flow: column nowrap;
  justify-content: center;
  align-items: center;
  box-sizing: border-box;
}

.piano-key p {
  font-size: 10px;
}

.piano-key.black {
  background-color: #0D0D0D;
  background-color: var(--black);
  color: #D5D5D5;
  color: var(--light-grey);
}

.piano-key.white {
  background-color: #D5D5D5;
  background-color: var(--light-grey);
  color: #0D0D0D;
  color: var(--black);
}

.cell-trigger-synth1 {
  background-color: #26C6C6;
  background-color: var(--synth1Color);
  border-radius: 5px;
  border-color: #26C6C6;
  border-color: var(--synth1Color);
}

.cell-trigger-bass {
  background-color: #FF6AEE;
  background-color: var(--bassColor);
  border-radius: 5px;
  border-color: #FF6AEE;
  border-color: var(--bassColor);
}

.cell-trigger-lenny {
  background-color: yellow;
  background-color: var(--lennyColor);
  border-radius: 5px;
  border-color: yellow;
  border-color: var(--lennyColor);
}

.cell-trigger-drummer {
  background-color: green;
  background-color: var(--drummerColor);
  border-radius: 5px;
  border-color: green;
  border-color: var(--drummerColor);
}

.cell-trigger {
  width: 100%;
  height: 100%;
}
html {
  font-family: 'Open Sans', sans-serif;
}

body {
  margin: 0;
}

h1 {
  font-family: 'Gothic A1', 'Helvetica', sans-serif;
  font-weight: 900;
}

h2 {
  font-family: 'Gothic A1', 'Helvetica', sans-serif;
  font-size: 24px;
  font-weight: 900;
}

main {
  position: absolute;
  width: 100vw;
  height: 100vh;
  display: grid;
  grid-template-areas: 
  "head  head   head" 
  ".     top       ." 
  "left center right" 
  ".    bottom    . ";
  grid-template-columns: 15vw minmax(0, auto) 15vw;
  grid-template-rows: 60px 15vh minmax(0, auto) 15vh
}

#control-panel {
  grid-area: head;
  display: flex;
}

#sequencer {
  grid-area: center;
  width: 100%;
  height: 100%;
}

#play-button {
  width: 60px;
  margin: auto;
  display: block;
}

.synth {
  border: 1px solid lightgray;
}

.bass-synth {
  grid-area: bottom;
  display: flex;
  justify-content: space-around;
}

.bass-synth h2 {
  line-height: 1;
  margin: 12px 12px 0 12px;
}

.synth-1 {
  grid-area: left;
  display: flex;
  justify-content: center;
}

.lenny-synth {
  grid-area: top;
  display: flex;
  justify-content: space-around;
}

.knob {
  display: flex;
  flex-direction: column;
  align-content: center;
  text-align: center;
  padding: 8px;
  line-height: 2;
  font-family: 'Gothic A1', 'Helvetica', sans-serif;
  font-weight: 500;
  font-size: 12px;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.knob-row {
  display: flex;
}

.knob-row-horizontal {
  flex-direction: row;
  justify-content: center;
}

.knob-row-vertical {
  flex-direction: column;
  justify-content: flex-start;
}
